@import url(https://fonts.googleapis.com/css?family=Quicksand:400,300,700);
// @import '../../../utils.scss';

.timer-container {
  //position: absolute;
  left:0;
  bottom:0;
  text-align: center;
  box-sizing: border-box;
  width: 100%;
  margin: 0;
  padding: 20px;
  font-family: "Quicksand", sans-serif;
  background-color: #1c1c1c;
  
  #timer-content {
    max-width: 600px;
    min-height: 24px;
    margin: 0 auto;
    text-align: center;

    p {
      color: #fff;
      font-size: 1.8rem;
      font-weight: 400;
      margin: 0 auto;
      padding: 0;
      letter-spacing: 0.2rem;
      text-transform: uppercase;
      word-break: keep-all;

      &:last-child {
        font-size: 0.75rem;
        font-weight: 700;
        margin: 3em auto;
        padding: 0;
        letter-spacing: 0.1rem;
      }
    }
    span {
      font-size: 0.9rem;
      color: #E6E6E6;
    }

    // @include media('<medium'){
    //   p {
    //     color: #fff;
    //     font-size: 1.4rem;
    //     font-weight: 400;
    //     margin: 0 auto;
    //     padding: 0;
    //     letter-spacing: 0.2rem;
    //     text-transform: uppercase;
    //     word-break: keep-all;

    //     &:last-child {
    //       font-size: 0.75rem;
    //       font-weight: 700;
    //       margin: 3em auto;
    //       padding: 0;
    //       letter-spacing: 0.1rem;
    //     }
    //   }

    //   span{
    //     font-size: 0.75rem;
    //   }

    // }

    section {
      color: #c7e7fb;
      margin: 0 auto;
      line-height: 24px;
      font-size: 1rem;
      font-weight: 700;
      
      ul {
        list-style-type: none;
        width: 100%;
        padding: 0;
        align-items: center;

        li {
            display: inline-block;
            //margin-right: 2rem;
            width: 6rem;
        }
      }

      // @include media('<medium'){
      //   ul {
      //     list-style-type: none;
      //     display: inline-flex;
      //     width: 100%;
      //     padding: 0;
      //     align-items: center;

      //     li {
      //       display: inline-block;
      //       //margin-right: 2rem;
      //       width: 6rem;
      //     }
      //   }
      // }

      .timer-number {
        display: block;
        font-size: 1.3rem;
        font-weight: 400;
        line-height: 1.5rem;
        margin: 0 auto;
        text-align: center;
      }

      .timer-word {
        font-size: 0.5rem;
        font-variant: small-caps;
        line-height: 1.5rem;
        margin: 0 auto;
        text-align: center;
        position: relative;
        top: 0px;
      }
    }


  }
}

